.banner-container {
  min-height: 400px;
  background-image: url("/public/main.jpg");
  padding-top: 100px;
  padding-bottom: 100px;
  font-family:ubuntu,sans-serif;

  &__title {
    display: inline-grid;
    font-family:ubuntu,sans-serif;
    text-transform: uppercase;

    div:first-child {
      font-size: 30px;
      color: #FFFFFF;
      margin-bottom: 1px;
      
      span {
        background-color: #A9A9A9;
        padding-left: 5px;
        padding-right: 5px;
      }
    }
    div:nth-child(2) {
      font-size: 50px;
      color: #FFFFFF;
      background-color: #395ACB;

      span {
        padding-left: 5px;
        padding-right: 5px;
      }
    }

    div:last-child {
      font-size: 22px;
      padding-top: 15px;
      padding-bottom: 15px;
    }
  }

  &__list {
    font-size: 20px;
    font-weight: 300;
    padding-inline-start: 0;

    li {
      span {
        padding-right: 5px;
        color: #395ACB;
      }
      list-style-type: none;
    }
  }

  &__banner-button {
    font-family: 'Roboto',sans-serif;
    font-weight: 300;
    word-break: break-word;
    width: 200px;
    color: #fff;
    font-size: 16px;
    cursor: pointer;
    border: 3px solid transparent;
    background: #2E7E28;
    border-radius: 0;
    padding: 10px 20px;
    margin: 20px auto 0;

    &:hover {
      background-color: #3d593b;
    }
  }
}